@use "global" as *;

.sub-title {
    font-family: $second-font-family;
    font-weight: 500;
    font-size: max(rem(22),18px );
    position: relative;
    margin-left: rem(50);
    line-height: 1.5909090909;
        &::before {
        content: '';
        position: absolute;
        background-image: url(../images/screening/sp_icon.png);
        background-position: center center;
        background-repeat: no-repeat;
        background-size: contain;
        width: rem(32);
        height: rem(32);
        left: rem(-40);
        vertical-align: middle;
    }

    @include mq('xl') {
        font-size: rem(30);
        margin-left: 0;
    }

}